import React from "react";
import ReactDOM from "react-dom";
import Loader from "react-loader-spinner";
const LoaderHOC = (propName) => (WrappedComponent) => {
  return class LoaderHOC extends React.Component {
    isEmpty(prop) {
      return prop === null;
    }
    render() {
      return this.isEmpty(this.props[propName]) ? (
        <Loader type="ThreeDots" color="#00BFFF" height={80} width={80} />
      ) : (
        <WrappedComponent {...this.props} />
      );
    }
  };
};

var HeaderData = function (props) {
  return <h1>{props.ComponentToDisplay}</h1>;
};

var DisplayData = LoaderHOC("ComponentToDisplay")(HeaderData);

class LoaderData extends React.Component {
  state = { ComponentToDisplay: null };

  componentDidMount() {
    setTimeout(() => {
      this.setState({ ComponentToDisplay: "This is the example of loader in Reactjs" });
    }, 1000);
  }

  render() {
    return (
      <h1 className="text-muted text-center p-3 m-5">
        <DisplayData ComponentToDisplay={this.state.ComponentToDisplay} />
      </h1>
    );
  }
}

export default LoaderData;
 